.login{
    display: grid;
    place-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 100px;
    padding-bottom: 320px;
}

.login > img{
    object-fit: contain;
    height: 70px;
    margin-top: 20px;
    margin-bottom: 20px;

}

.login > form{
    display: flex;
    flex-direction: column;

}
.login > form > input {
    width: 350px;
    height: 50px;
    font-size: 20px;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;

}

.login > form > button {
    width: 365px;
    height: 50px;
    font-size: large;
    color: white;
    background-color: #787A91;
    border-radius: 10px;

}
.login > form > button:hover{
    background-color: #EEEEEE ;
    cursor: pointer;
    color: black;


}
.login__register{
    color:#787A91;
    cursor: pointer;
}
.login > p{
    margin-top: 20px;
    color: whitesmoke;
}